import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "../views/HomeView.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    redirect: "/login",
  },
  {
    path: "/login",
    name: "login",
    component: () => import("../views/LoGin.vue"),
  },
  {
    path: "/home",
    name: "home",
    component: HomeView,
    redirect: {
      name: "about",
    },
    //路由嵌套
    children: [
      {
        path: "/about",
        name: "about",
        component: () =>
          import(/* webpackChunkName: "about" */ "../views/AboutView.vue"),
        redirect: {
          name: "one",
        },
        children: [
          {
            path: "/about/one",
            name: "one",
            component: () => import("../views/about/AboutOne.vue"),
          },
          {
            path: "/about/two",
            name: "two",
            component: () => import("../views/about/AboutTwo.vue"),
          },
          {
            path: "/about/three",
            name: "three",
            component: () => import("../views/about/AboutThree.vue"),
          },
        ],
      },
      {
        path: "/Enter",
        name: "enter",
        component: () =>
          import(/* webpackChunkName: "about" */ "../views/EnterPrise.vue"),
        //默认第一个
        redirect: {
          path: "/list-item-8",
        },
        children: [
          {
            path: "/list-item-:id",
            name: "enterson",
            component: () => import("../views/enter/EnterSon.vue"),
          },
        ],
      },
      {
        path: "/system",
        name: "system",
        component: () => import("../views/CharacterSystem.vue"),
      },
      {
        path: "/callto",
        name: "callto",
        component: () => import("../views/CalltotransferView.vue"),
      },

      {
        path: "/number",
        name: "number",
        component: () => import("../views/NumbermanagementView.vue"),
      },
    ],
  },
];

const router = new VueRouter({
  routes,
});

export default router;
//路由守卫
router.beforeEach((to, from, next) => {
  var token = localStorage.getItem("token");
  console.log(token);
  if (to.name === "login") {
    next();
    // 修改这里，使用 || 而不是 &&
  } else {
    if (token === null || token === "") {
      next("/"); // 如果token无效或不存在，则跳转到登录页
      return;
    }
    next();
  }
});
